.header {
  margin-bottom: 56px;
  &__top {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-top: 41px;
    margin-bottom: 19px;
  }

  &__time {
    text-align: center;
    font-size: 13px;
  }

  &__tel {
    font-family: var(--neueMachina);
    font-size: 18px;
    font-weight: 500;
    max-width: 190px;
    text-align: end;
    span {
      text-decoration: underline;
      display: block;
    }
  }

  &__bottom {
    display: flex;
    align-items: center;
  }

  &__burger {
    width: 54px;
    height: 42px;
    margin-right: 4px;
    padding: 0;
    background-color: var(--orange400);
    display: flex;
    align-items: center;
    justify-content: center;
  }

  &__catalog, &__search {
    margin-right: auto;
  }

  &__search {
    background-color: var(--orange400);
    padding: 2px;
    max-width: 556px;
    width: 100%;

  }
}
