.question {
  background-color: #E9540D;
  padding: 40px 0;
  margin-bottom: 20px;
  &__inner {
    background-color: #2C2C2C;
    padding: 10px;
  }

  &__content {
    padding: 50px 32px;
    border: 2px solid var(--orange400);
    text-align: center;
    position: relative;
    &::before, &::after {
      position: absolute;
      content: '';
      top: 0;
      bottom: 0;
      width: 2px;
      background-color: var(--orange400);
    }

    &::before {
      left: 15%;
    }

    &::after {
      right: 15%;
    }
  }

  &__text {
    max-width: 705px;
    margin: 0 auto 15px;
    font-family: var(--neueMachina);
    font-weight: 800;
    font-size: 20px;
    line-height: 1;
  }

  &__list {
    display: flex;
    justify-content: center;
  }

  &__item {
    &:not(:last-child) {
      margin-right: 45px;
    }
  }

  &__link {
    position: relative;
    display: block;
    font-size: 15px;
    padding-left: 28px;
    transition: color 0.2s;
    &:before {
      content: '';
      position: absolute;
      left: 0;
      top: 0;
      bottom: 0;
      margin: auto 0;
      background-repeat: no-repeat;
      background-position: center center;
    }

    &--tel {
      &::before {
        background-image: url('../img/tel-icon.svg');
        width: 14px;
        height: 16px;
      }
    }

    &--email {
      &::before {
        background-image: url('../img/email-icon.svg');
        width: 18px;
        height: 12px;
      }
    }

    &:focus {
      box-shadow: 0 0 0 2px rgba(255, 104, 56, 0.7);
    }

    &:hover {
      color: var(--orange400);
    }

    &:active {
      color: darken($color: #fff, $amount: 15%);
    }

  }
}
