.about {
  margin-bottom: 35px;
  text-align: center;
  &__title {
    margin-bottom: 35px;
  }

  &__text {
    max-width: 860px;
    margin: 0 auto;
    font-family: var(--neueMachina);
    font-weight: 800;
    font-size: 15px;
    p {
      margin: 0;

      &:first-child {
        color: var(--orange400);
        font-size: 20px;
      }

      &:not(:last-child) {
        margin-bottom: 10px;
      }
    }
  }
}
