.description {
  margin-bottom: 45px;

  &__inner {
    display: flex;
    justify-content: space-between;
  }

  &__content {
    max-width: 475px;
    height: 100%;
    font-size: 15px;
    padding: 50px 26px;
    border: 2px solid var(--orange400);
    position: relative;

    &::before,
    &::after {
      content: '';
      position: absolute;
      height: 2px;
      background-color: var(--orange400);
      left: 0;
      right: 0;

    }

    &::before {
      top: 16px;
    }

    &::after {
      bottom: 16px;
    }

    p {
      margin: 0;
      &:not(:last-child) {
        margin-bottom: 10px;
      }
    }

    span {
      color: var(--orange400);
      font-family: var(--neueMachina);
      font-weight: 800;
      font-size: 18px;
    }
  }

  &__link {
    &:hover {
      text-decoration: underline;
    }
  }

  &__list {
    display: flex;
  }

  &__item {
    &:not(:last-child) {
      margin-right: 24px;
    }
  }

  &__icon {
    width: 120px;
    height: 110px;
    border: 2px solid var(--orange400);
    margin-bottom: 13px;
    background-repeat: no-repeat;
    background-position: center center;

    &--solutions {
      background-image: url('../img/descr-img/solutions.png');
    }

    &--distributors {
      background-image: url('../img/descr-img/distributors.png');
    }

    &--terms {
      background-image: url('../img/descr-img/terms.png');
    }
  }

  &__text {
    max-width: 140px;
    p {
      margin: 0;
      &:not(:last-child) {
        margin-bottom: 10px;
      }
    }
  }
}
