.car-brands {
  margin-bottom: 44px;
  &__title {
    margin-bottom: 25px;
  }

  &__logos {
    width: 100%;
    border: 2px solid var(--orange400);
    border-collapse: collapse;
  }

  td {
    border: 2px solid var(--orange400);
    padding: 10px 30px;
    transition: background-color 0.2s;
    &:hover {
      background-color: var(--orange400);
    }
  }

  &__img {
    margin: 0 auto;
  }

  &__list {
    border: 2px solid var(--orange400);
    border-top: none;
    column-count: 5;
    column-gap: 55px;
    padding: 43px 40px;
  }

  &__item {
    font-family: var(--neueMachina);
    color: var(--orange400);
    font-size: 20px;
    font-weight: 800;
    text-transform: uppercase;
    &:not(:last-child) {
      margin-bottom: 5px;
    }
  }

}
