.socials {
  display: flex;
  &__item {
    &:not(:last-child) {
      margin-right: 7px;
    }
  }

  &__link {
    display: block;
    width: 27px;
    height: 27px;
    background-repeat: no-repeat;
    background-position: center center;
    background-size: cover;

    &--facebook {
      background-image: url('../img/socials/facebook.png');
    }

    &--vk {
      background-image: url('../img/socials/vkontakte.png');
    }
  }
}
