.contacts {

  &__wrapper {
    border: 1px solid var(--orange400);
    padding: 58px 37px 80px 65px;
  }

  &__title {
    margin-top: 0;
    margin-bottom: 80px;
  }

  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__content {
    align-self: end;
  }

  &__phones {
    margin-bottom: 72px;
  }

  &__heading {
    color: var(--orange400);
    font-size: 30px;
    font-weight: 700;
    margin-bottom: 10px;
  }

  &__link, &__text {
    font-size: 18px;
  }

  &__link {
    &:focus {
      box-shadow: 0 0 0 2px rgba(255, 104, 56, 0.7);
    }

    &:hover {
      color: var(--orange400);
    }

    &:active {
      color: darken($color: #fff, $amount: 15%);
    }
  }

  &__map {
    text-align: center;
  }

  &__img {
    width: 523px;
    height: 264px;
    background-color: #C4C4C4;
  }

}
