.footer {
  padding: 35px 0;

  &__top {
    display: flex;
    justify-content: space-between;
    margin-bottom: 47px;
  }

  &__logo {
    align-self: center;
  }

  &__text {
    margin-bottom: 12px;
    font-family: var(--neueMachina);
    color: var(--orange400);
    font-size: 20px;
    font-weight: 800;
  }

  &__item {
    &:not(:last-child) {
      margin-bottom: 5px;
    }
  }

  &__link {
    transition: color 0.2s;
    &--active {
      color: var(--orange400);
    }

    &:focus {
      box-shadow: 0 0 0 2px rgba(255, 104, 56, 0.7);
    }

    &:hover {
      color: var(--orange400);
    }

    &:active {
      color: darken($color: #fff, $amount: 15%);
    }
  }

  &__bottom {
    display: flex;
    align-items: center;
  }

  &__copyright {
    margin-right: 230px;
  }

  &__privacy {
    display: block;
    color: var(--orange400);
    &:hover {
      text-decoration: underline;
    }
  }
}
