.car-year, .equipment {

  &__title {
    margin-bottom: 25px;
  }

  &__list {
    display: flex;
    flex-wrap: wrap;
    margin-top: -20px;
    margin-right: -45px;
  }

  &__item {
    margin-top: 20px;
    margin-right: 45px;
  }

  &__link {
    display: block;
    padding: 30px 20px;
    text-align: center;
    min-width: 217px;
    background-color: var(--orange400);
    font-family: var(--neueMachina);
    font-size: 25px;
    font-weight: 800;
    text-transform: uppercase;
    &:focus {
      box-shadow: 0 0 0 2px rgba(255, 60, 0, 0.7);
    }

    &:hover {
      color: var(--orange400);
      background-color: transparent;
      box-shadow: 0 0 0 2px rgba(255, 104, 56, 0.7);
    }

    &:active {
      opacity: 0.7;
    }
  }
}
