.news-card {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  border: 2px solid var(--orange400);
  padding: 26px 8px 50px 20px;
  height: 100%;
  &__img {
    object-fit: cover;
    margin-bottom: 16px;
  }

  &__title {
    color: var(--orange400);
    font-weight: 700;
    font-size: 25px;
    margin-bottom: 20px;
  }

  &__time {
    color: var(--orange400);
    font-size: 13px;
  }

  &__list {
    margin-bottom: 35px;
  }


  &__line {
    display: inline-block;
    vertical-align: middle;
    width: 13px;
    height: 2px;
    background-color: var(--orange400);
  }

  &__btn {
    margin-top: auto;
    text-decoration: underline;
    font-size: 15px;
  }
}
