.team {
  padding: 35px 0 40px;
  margin-bottom: 40px;
  border-top: 2px solid var(--orange400);
  border-bottom: 2px solid var(--orange400);

  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__img {
    height: 100%;
  }

  &__content {
    max-width: 490px;
    border: 2px solid var(--orange400);
    padding: 34px 30px;
  }

  &__title {
    font-size: 18px;
    color: var(--orange400);
  }

  &__text {
    line-height: 1.3;
    font-size: 15px;
  }
}
