// если вы хотите использовать sass-переменные - удалите root
// colors
:root {
  // base
  --font-family: "Gill_Sans_MT", Tahoma, sans-serif;
  --content-width: 990px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));

  // colors
  --white300: #FEFEFE;
  --orange400: #FF4F00;
  --black600: #2C2C2C;

  //vars fonts
  --neueMachina: 'NeueMachina';
  --roboto: 'Roboto';
}
