.form-search {
  position: relative;

  &__input {
    width: 100%;
    height: 42px;
    font-size: 16px;
    padding: 0 55px 0 10px;
    border: none;
  }

  &__btn {
    position: absolute;
    right: 10px;
    bottom: 0;
    top: 0;
    width: 44px;
    background-image: url('../img/search-icon.png');
    background-repeat: no-repeat;
    background-position: center center;
  }
}
