.contacts__link:active {
  color: #d9d9d9;
}
.contacts__map {
  text-align: center;
}
.contacts__img {
  width: 523px;
  height: 264px;
  background-color: #C4C4C4;
}

.model-selection__title {
  margin-bottom: 30px;
}
.model-selection__list {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 20px 20px;
}
.model-selection__link {
  background-color: rgba(44, 44, 44, 0.5);
  background-blend-mode: overlay;
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
  display: block;
  border: 2px solid var(--orange400);
  max-width: 231px;
  width: 100%;
  height: 197px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: var(--neueMachina);
  font-size: 25px;
  font-weight: 800;
  text-transform: uppercase;
}
.model-selection__link:focus {
  box-shadow: 0 0 0 2px rgba(255, 104, 56, 0.7);
}
.model-selection__link:hover {
  color: var(--orange400);
}
.model-selection__link:active {
  opacity: 0.7;
}

.car-year__title, .equipment__title {
  margin-bottom: 25px;
}
.car-year__list, .equipment__list {
  display: flex;
  flex-wrap: wrap;
  margin-top: -20px;
  margin-right: -45px;
}
.car-year__item, .equipment__item {
  margin-top: 20px;
  margin-right: 45px;
}
.car-year__link, .equipment__link {
  display: block;
  padding: 30px 20px;
  text-align: center;
  min-width: 217px;
  background-color: var(--orange400);
  font-family: var(--neueMachina);
  font-size: 25px;
  font-weight: 800;
  text-transform: uppercase;
}
.car-year__link:focus, .equipment__link:focus {
  box-shadow: 0 0 0 2px rgba(255, 60, 0, 0.7);
}
.car-year__link:hover, .equipment__link:hover {
  color: var(--orange400);
  background-color: transparent;
  box-shadow: 0 0 0 2px rgba(255, 104, 56, 0.7);
}
.car-year__link:active, .equipment__link:active {
  opacity: 0.7;
}

.equipment__link {
  text-align: center;
  display: block;
  min-width: 182px;
  background-color: transparent;
  border: 2px solid var(--orange400);
}
.equipment__link:hover {
  background-color: var(--orange400);
  color: #fff;
}